    transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  }
  .modal-overlay.active {
    opacity: 1;
    visibility: visible;
  }
  .modal {
    position: absolute;
    top: 0px;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-overflow-scrolling: touch;
    border-radius: 0;
    transform: scale(1.1);
    padding: 0 !important;
  }
  .modal .modal-content {
    position: absolute;
    top: 0px;
    left: 0;
  }
  .close-modal {
    right: 20px !important;
    z-index: 1;
  }
}
/* Overlay */
.modal-overlay {
  display: none;
}
.modal-overlay.active {
  overflow-y: scroll;
}
.modal-overlay.show {
  display: flex;
}

